* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, rgb(193, 255, 228) 0%, rgb(214, 196, 255) 100%);
  transition: all 2s ease;
}

body.winner-one {
  background: rgb(193, 255, 228);
}

body.winner-two {
  background: rgb(214, 196, 255);
}

body.death {
  background: #000;
}

header {
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  padding: 10px 0 10px 25px;
  letter-spacing: 27px;
  color: #fff;
  text-shadow: 1px 1px #4a2afb;
}

.winner-one header {
  text-shadow: 1px 1px #fff;
  color: rgb(27, 179, 110);
}

.winner-two header {
  text-shadow: 1px 1px #fff;
  color: #8e41f3;
}

.death header {
  text-shadow: 1px 1px #fff;
  color: #000;
}

header .team-1 {
  text-shadow: 1px 1px #fff;
  color: rgb(27, 179, 110);
}

header .team-2 {
  text-shadow: 1px 1px #fff;
  color: #8e41f3;
}

button {
  cursor: pointer;
  outline: none;
}

.game-board {
  padding: 2px;
  width: 100%;
  max-width: 87vh;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.game-board li {
  list-style-type: none;
  width: calc(20% - 2px);
  height: 0;
  padding-top: calc(20% - 2px);
  margin: 1px;
  position: relative;
}

.game-board li button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  border: 0;
  border-radius: 0;
  background-color: rgba(255, 255, 255, .33);
  color: #274292;
  text-transform: uppercase;
  transition: all .4s ease;
  word-break: break-word;
  padding: 2px;
  margin: 0;
  font-weight: 600;
}

.game-board li button.active-one {
  background-color: rgb(138, 210, 179);
  color: #19543f;
}

.game-board li button.active-two {
  color: #482382;
  background-color: rgb(191, 182, 226);
}

.game-board li button.active-neutral {
  color: #695c19;
  background-color: #fff7bb;
}

.game-board li button.active-death {
  color: #fff;
  background-color: #000;
}

.game-board li button:hover {
  box-shadow: 0 0 14px rgba(14, 0, 230, 0.15);
}

.game-board li button:disabled {
  cursor: not-allowed;
}

nav {
  text-align: center;
  margin-top: .5rem;
}

nav button {
  border: 0;
  border-radius: 2em;
  background-color: rgba(255, 255, 255, .5);
  text-transform: uppercase;
  padding: 0.4rem .5rem;
  font-weight: 700;
  color: #4a2afb;
  transition: all .4s ease;
  margin: 0 .5rem;
}

nav button:hover {
  box-shadow: 1px 1px 6px rgba(35, 20, 158, 0.15);
}

@media screen and (max-width: 500px) {
  header {
    letter-spacing: 15px;
  }
}
